<template>
    <div class="flex-page">
        <van-nav-bar class="flex-full" ref="flexFull">
            <div slot="left" class="leftBar whiteNavBar">
                <div class="backIcon" @click="goBack()">
                    <img :src="isNavBar?icon.backBlack:icon.backWhite" alt="">
                </div>
                <div class="searhInputBox">
                    <div class="searchInput" @click="goToGoodsSearch()">搜索好物</div>
                    <img class="voiceIcon" src="@/assets/images/mall/mall_icon_voice.png" alt="" v-show="isShowVoiceBtn" @click="goToSpeech">
                    <dialog-bar v-model="sendVal" type="danger" v-bind:title="recognizeResult" content="您可以说：“花生油”<br />“我想要买花生油”<br />..." v-on:cancel="clickCancel()" @danger="clickDanger()" @confirm="clickConfirm()" dangerText="Delete" ></dialog-bar>
                </div>
                <div class="searchBtn">
                    <img class="collectBtn" :src="isNavBar?(isCollect?icon.collectionBlackSel:icon.collectionBlackNor):
                    (isCollect?icon.collectionSel:icon.collectionNor)" @click="collectBtn()" alt="">
                    <div class="shopIcon" @click="goToShoppingCart()">
                        <img ref="cartIcon" :src="isNavBar?icon.shopCartBlack:icon.shopCartWhite" alt="">
                        <div class="goodsNum" :class="skuTypeNum>=100?'goodsNumMax':''" v-show="skuTypeNum!=0">
                            {{skuTypeNum<=99?skuTypeNum:'99+'}}
                        </div>
                    </div>
                </div>
            </div>
        </van-nav-bar>
        <div class="post-wrapper" :class="isScrollWrapper?'postWrapper':''" ref="scrollContent" :style="{height:goodsListHeight+'px'}" @scroll="paperScroll()">
            <div class="shopBar" :class="this.$devicePlatform == 'ios'?'iosShopBar':''">
                <div class="shopInfo">
                    <div class="shopImg">
                        <img :src="shopInfo.shopPicUrl" alt="">
                    </div>
                    <div class="shopInfoBox">
                        <div class="shopName">{{shopInfo.shopName}}</div>
                        <div class="shopStarAndGrade">
                            <div class="shopStarBox" v-if="shopInfo.shopLevel!=0">
                                <img v-for="shopLevelItem in shopInfo.shopLevel" :key="'shopLevelItem'+shopLevelItem"
                                     src="@/assets/images/common/icon_evaluate_red.png" alt="">
                                <img v-for="noShopLevelItem in 5-shopInfo.shopLevel"
                                     :key="'noShopLevelItem'+noShopLevelItem"
                                     src="@/assets/images/common/icon_evaluate_gray.png" alt="">
                            </div>
                            <div class="gradeNum" v-if="shopInfo.shopLevel!=0">{{shopInfo.shopLevel}}</div>
                            <div class="gradeNumTxt" v-if="shopInfo.shopLevel==0">未评价</div>
                            <div class="saleNum">月售{{shopInfo.orderCount | threshold}}单</div>
                        </div>

                    </div>
                </div>
            </div>
            <div class="shopCouponBox" v-if="pickupList.length!=0">
                <div class="shopCouponList">
                    <!-- <div class="shopCouponItem"
                        :class="pickupItem.isFinished==1?'grayCouponInfo':(pickupItem.isPicked==1?'receiveShopCoupon':'')"
                        v-for="(pickupItem,pickupIndex) in pickupList" :key="'pickupIndex'+pickupIndex"> -->
                    <div class="shopCouponItem"
                         :class="pickupItem.isPicked==1?'receiveShopCoupon':(pickupItem.isFinished==1?'grayCouponInfo':'')"
                         v-for="(pickupItem,pickupIndex) in pickupList" :key="'pickupIndex'+pickupIndex">
                        <div class="shopCouponContent">
                            <div class="shopCouponPrice">
                                <div class="couponPriceIcon">￥</div>
                                <div class="couponPriceNum">{{pickupItem.couponTotal}}</div>
                            </div>
                            <div class="fullReductionTxt">{{pickupItem.name}}</div>
                            <div class="receiveImmedBtn" v-if="pickupItem.isPicked==0&&pickupItem.isFinished!=1"
                                 @click="receiveBtn(pickupItem)">立即领取
                            </div>
                            <div class="receiveImmedBtn receivedBtn"
                                 v-else-if="pickupItem.isPicked==1">已领取
                            </div>
                            <div class="receiveImmedBtn grayBtn" v-else-if="pickupItem.isFinished==1">已领完</div>

                        </div>
                    </div>
                </div>
            </div>
            <div class="goodsListBox">
                <van-sidebar class="sideBar" ref="leftSideBar" :style="{maxHeight:goodsListHeight+'px'}"
                             :class="isSideBarFixed?'sideBarFixed':''" v-model="activeKey">
                    <van-sidebar-item v-for="(leftClassItem,leftClassIndex) in leftClassList"
                                      :key="'leftClassIndex'+leftClassIndex" :title="leftClassItem.shopClassName"
                                      @click="switchLeftClass" />

                    <!--                                <van-sidebar-item v-for="(leftClassItem,leftClassIndex) in leftClassList" :key="leftClassIndex" :title="leftClassItem.shopClassName" @click="switchLeftClass"/>-->
                </van-sidebar>
                <div class="classifyList" ref="sideBar" :style="{height:goodsListHeight+'px'}"
                     :class="isSideBarFixed?'classifyListFixed':''" @scroll="classifyScroll()" @touchstart="handleTouchStart"
                     @touchend="handleTouchEnd"
                     @touchmove="handleTouchMove">
                    <div id="classifyList" style=""></div>
                    <div class="classifyTitle" ref="classifyTitle">
                        <div class="classifyTxt">{{leftClassList[activeKey].shopClassName}}</div>
                        <div class="salePriceSort">
                            <div class="saleSort" @click="sortMethod=3">
                                <div>销量</div>
                                <div class="redLineBox" v-show="sortMethod==3"><img
                                        src="@/assets/images/mall/redLine.png" alt=""></div>
                            </div>
                            <div class="priceSortBox" @click="priceSort()">
                                <div class="priceSort">
                                    <div class="priceSortTxt">价格</div>
                                    <img :src="sortMethod==''||sortMethod==3?icon.filter:(sortMethod==1?icon.filterUp:icon.filterDown)"
                                         alt="">
                                </div>
                                <div class="redLineBox" v-show="sortMethod==1||sortMethod==2"><img
                                        src="@/assets/images/mall/redLine.png" alt=""></div>
                            </div>
                        </div>
                    </div>
                    <div ref="vanList" :style="{minHeight:vanListHeight+'px'}">
                        <div class="goodsBox" v-for="(goodsItem,goodsIndex) in goodsList"
                             @click="goToProduct(goodsItem)" :key="'goodsIndex'+goodsIndex">
                            <div class="goodsItem">
                                <div class="goodsImg">
                                    <!--                                    <img v-lazy="goodsItem.spuPicUrl" alt="" :key="goodsItem.spuPicUrl">-->
                                    <img :src="goodsItem.spuPicUrl" alt="">
                                    <div class="showActivitySpu" v-show="goodsItem.activitySpuId">秒杀</div>
                                    <div class="outSellBox" v-show="goodsItem.storeNumber==0&&!goodsItem.activitySpuId">补货中</div>
                                    <div class="jinshengNum" v-show="goodsItem.storeNumber<=goodsItem.threshold&&goodsItem.storeNumber!=0&&!goodsItem.activitySpuId">仅剩{{goodsItem.storeNumber}}件</div>
                                </div>
                                <div class="goodsInfo">
                                    <div class="productInfo">
                                        <div class="goodsName">{{goodsItem.spuName}}</div>
                                        <div class="goodsDesc">{{goodsItem.spuSalePoint}}</div>
                                        <div class="goodsSale">月售{{goodsItem.showSale | threshold}}</div>
                                    </div>
                                    <div class="goodsPriceBx">
                                        <div class="goodsPriceInfo">
                                            <div class="goodsPrice">
                                                <div class="goodsPriceIcon">￥</div>
                                                <div class="goodsPriceNum">{{goodsItem.skuPrice|priceFilters}}</div>
                                            </div>
                                            <div class="goodsLinePrice" v-if="goodsItem.priceLine">
                                                ￥{{goodsItem.priceLine|priceFilters}}
                                            </div>
                                        </div>
                                        <div class="addMinusBtn">
                                            <div class="clickBox" @click.stop="addGoodsNum(goodsItem,goodsIndex)"></div>
                                            <div class="cartNumBox" v-show="goodsItem.cartCount!=0" :class="goodsItem.cartCount>99?'cartNumBigBox':''">
                                                <div class="cartNum">{{goodsItem.cartCount}}</div>
                                            </div>
                                            <img class="addBtn" ref="addBtn" src="@/assets/images/mall/mall_icon_add.png" alt="" @click.stop="addGoodsNum(goodsItem,goodsIndex)">
                                            <!-- <img class="addBtn" ref="addBtn" v-show="goodsItem.storeNumber!=0" src="@/assets/images/mall/mall_icon_add.png" alt="" @click.stop="addGoodsNum(goodsItem,goodsIndex)"> -->
                                            <!-- <img class="addBtn" v-show="goodsItem.storeNumber==0" src="@/assets/images/mall/mall_icon_add_gray.png" alt="" @click.stop="addGoodsNum(goodsItem,goodsIndex)"> -->
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="lineBox" v-show="goodsIndex != goodsList.length-1"></div>
                        </div>
                        <div class="goodsBlankBox" v-show="goodsList.length==0&&isloading">
                            <img src="@/assets/images/default/no_shop_con_img.png" alt="">
                            <div>该分类无商品</div>
                        </div>
                    </div>
                    <div class="finishedBox" ref="finishedBox" :style="{height:'43px',lineHeight:'43px',paddingBottom:this.$stbHeight+'px'}" v-show="finished&&goodsList.length!=0">
                        <img src="@/assets/images/mall/mall_logo.png" alt="">
                    </div>
                </div>
            </div>
        </div>
        <transition @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
            <img class="circle" v-show="isShowCircle" src="@/assets/images/mall/mall_icon_add.png" alt="">
        </transition>
    </div>
</template>


<script>
    import {shopPage} from '../../../page/superiorProduct/shopPage'
    export default {
        name: 'shopPageThemeL',
        mixins:[shopPage]
    }
</script>

