        position: absolute;
        top: 0;
        width: 40px;
        height: 16px;
        background: url("../../../assets/images/mall/Spike_labelcut.png") no-repeat;
        -webkit-background-size: cover;
        background-size: cover;
        text-align: center;
        line-height: 16px;
        font-family: PingFangSC-Regular;
        font-size: 10px;
        color: #FFFFFF;
        letter-spacing: 0;
    }
    .circle{
        width: 22px;
        height: 22px;
        position: fixed;
        z-index: 100;
        transition: all .3s ease;
    }
    /deep/ .loading {
        display: none;
    }

    /deep/.van-sidebar-item{
        color: #333333;
        text-align: center;
        padding: 0.53333rem 0rem;
        .van-sidebar-item__text{
            overflow: hidden;
            white-space:nowrap;
        }
    }
    /deep/.van-sidebar-item--select{
        color: #FB5768;
        border-color: #FB5768;
    }
    .flex-page {
        width: 100%;
        height: 100%;
        // position: absolute;

        .post-wrapper {
            height: 100%;
            overflow: scroll;
        }
        .postWrapper{
            overflow: hidden;
        }
    }

    .leftBar {
        height: 46px;
        position: relative;
        z-index: 10;

        .backIcon {
            width: 32px;
            position: absolute;
            top: 0;
            left: -9px;
            z-index: 10;

            img {
                margin-top: 8px;
                width: 32px;
            }
        }

        .voiceIcon {
            width: 20px;
            height: 20px;
            position: absolute;
            top: 12px;
            right: 8px;
        }
    }

    .searchInput {
        width: 310px;
        height: 34px;
        background: #F7F7F7;
        border-radius: 2px;
        border: 0;
        padding-left: 32px;
        padding-right: 32px;
        font-family: PingFangSC-Regular;
        font-size: 13px;
        color: #9A9A9A;
        text-align: left;
        letter-spacing: -0.04px;
        line-height: 34px;
        background-image: url(../../../assets/images/mall/searchbar_icon_search_normal.png);
        background-repeat: no-repeat;
        -webkit-background-size: 14px 14px;
        background-size: 14px 14px;
        background-position: 10px 9px;
        position: relative;
        top: 6px;
        box-sizing: border-box;
        margin-left: 15px;
    }
    .leftBar {
        width: 100%;
        position: relative;
        padding-left: 15px;
        box-sizing: border-box;
        // position: fixed;
        // top: 0;
        display: flex;

        .searhInputBox {
            position: relative;
        }

        .searchInput {
            width: 250px;
            margin-right: 50px;
        }

        .voiceIcon {
            right: 58px;
        }

        .searchBtn {
            width: 82px;
            height: 34px;
            position: absolute;
            top: 6px;
            right: -33px;
            display: flex;

            align-items: center;

            img {
                width: 30px;
            }

            .collectBtn {
                margin-left: 5px;
            }

            .shopIcon {
                height: 30px;
                display: flex;
                /*margin-left: 5px;*/
                position: relative;

                /*.goodsNum {
                    width: 14px;
                    height: 14px;
                    text-align: center;
                    border-radius: 50%;
                    letter-spacing: -0.02px;
                    background: #FC0238;
                    position: absolute;
                    top: -3px;
                    right: -4px;
                    font-size: 9px;
                    color: #FFFFFF;
                    display:flex;
                    justify-content:center;
                    align-items:center
                }
                .goodsNumMax{
                    width: 18px;
                    border-radius: 10px;
                }*/
                .goodsNum {
                    width: max-content;
                    min-width: 19px;
                    height: 19px;
                    text-align: center;
                    border-radius: 9.5px;
                    letter-spacing: -0.02px;
                    background: #FC0238;
                    position: absolute;
                    top: -3px;
                    right: -4px;
                    font-size:  11px;
                    color: #FFFFFF;
                    box-sizing: border-box;
                    display:flex;
                    justify-content:center;
                    align-items:center;
                    line-height: 19px;
                }
                .goodsNumMax{
                    right: -12px;
                    padding: 0 3px;
                    min-width: 30px;
                }

            }
        }
    }
    .iosShopBar{
        margin-bottom: 1px;
    }
    .shopBar {
        height: 150px;
        background-image: linear-gradient(180deg, #FF9BA5 0%, #FB5364 98%);
