                width: 84px;
                height: 84px;
                overflow: hidden;
                img {
                    width: 100%;
                }
            }

            .shopInfoBox {
                margin-left: 15px;

                .shopName {
                    font-family: PingFangSC-Medium;
                    font-size: 20px;
                    color: #333333;
                    letter-spacing: -0.05px;
                    font-weight: 600;
                }

