                    display: flex;
                    align-items: center;
                    height: 15px;
                    margin-top: 8px;

                    .shopStarBox {
                        height: 15px;
                        display: flex;
                        align-items: center;

                        img {
                            width: 15px;
                            margin-right: 2px;
                        }
                    }
                    .gradeNumTxt {
                        font-family: PingFangSC-Medium;
                        font-size: 15px;
                        color: #9A9A9A;
                        letter-spacing: -0.04px;
                        line-height: 14px;
                        margin-left: 4px;
                    }

                    .gradeNum {
                        font-family: PingFangSC-Medium;
                        font-size: 15px;
                        color: #FB5364;
                        letter-spacing: -0.04px;
                        line-height: 14px;
                        margin-left: 4px;
                    }

                    .saleNum {
                        font-family: PingFangSC-Regular;
                        font-size: 15px;
                        color: #9A9A9A;
                        letter-spacing: -0.04px;
                        line-height: 14px;
                        margin-left: 20px;
                    }
                }
            }
        }
    }

