                    width: 100%;
                    height: 100%;
                    padding: 10px;
                    box-sizing: border-box;

                    .shopCouponPrice {
                        display: flex;
                        justify-content: center;

                        .couponPriceIcon {
                            font-family: PingFangSC-Semibold;
                            font-size: 14px;
                            color: #FFFFFF;
                            letter-spacing: -0.07px;
                            height: 28px;
                            padding-top: 9px;
                            box-sizing: border-box;
                        }

                        .couponPriceNum {
                            font-family: PingFangSC-Semibold;
                            font-size: 28px;
                            color: #FFFFFF;
                            letter-spacing: -0.14px;
                            line-height: 28px;
                            height: 28px;
                        }
                    }

                    .fullReductionTxt {
                        width: 110px;
                        font-family: PingFangSC-Regular;
                        font-size: 10px;
                        color: #FFFFFF;
                        letter-spacing: -0.03px;
                        text-align: center;
                        position: relative;
                        top: -2px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                    }

                    .receiveImmedBtn {
                        width: 86px;
                        height: 20px;
                        background: #FFFFFF;
                        box-shadow: 0 0 15px 0 rgba(207, 207, 207, 0.55);
                        border-radius: 15px;
                        font-family: PingFangSC-Medium;
                        font-size: 10px;
                        color: #FF9F00;
                        letter-spacing: -0.03px;
                        /*line-height: 20px;*/
                        /*text-align: center;*/
                        display:flex;
                        justify-content:center;
                        align-items:center;
                        margin: 0 auto;
                    }

                    .receivedBtn {
                        background: #C9A338;
                        box-shadow: 0 0 15px 0 rgba(207,207,207,0.55);
                        border-radius: 15px;
                        font-family: PingFangSC-Medium;
                        font-size: 10px;
                        color: #FFFFFF;
                        letter-spacing: -0.03px;

                    }
                    .grayBtn{
                        background: #ADADAD;
                        box-shadow: 0 0 15px 0 rgba(207,207,207,0.55);
                        border-radius: 15px;
                        font-family: PingFangSC-Medium;
                        font-size: 10px;
                        color: #FFFFFF;
                        letter-spacing: -0.03px;
                    }
                }
            }

            .receiveShopCoupon {
                background: url(../../../assets/images/mall/coupon_shop_receivesuccess.png) no-repeat;
                background-size: contain;
            }

            .grayCouponInfo {
                background: url(../../../assets/images/mall/coupon_shop_received.png) no-repeat;
                background-size: contain;
            }
        }
        .shopCouponList::-webkit-scrollbar {
            display: none;
        }
    }

