                    letter-spacing: -0.04px;
                    overflow: hidden;
                    white-space:nowrap;
                    text-overflow: ellipsis;
                }

                .salePriceSort {
                    display: flex;
                    align-items: center;
                    padding-right: 20px;

                    .saleSort {
                        font-family: PingFangSC-Regular;
                        font-size: 14px;
                        color: #4A4A4A;
                        letter-spacing: -0.03px;
                        line-height: 18px;
                        margin-right: 20px;
                        position: relative;

                        .redLineBox {
                            width: 20px;
                            height: 3px;
                            display: flex;
                            align-items: center;
                            position: absolute;
                            bottom: -3px;
                            left: 2px;

                            img {
                                width: 20px;
                            }
                        }
                    }

                    .priceSortBox {
                        position: relative;

                        .priceSort {
                            display: flex;
                            align-items: center;

                            .priceSortTxt {
                                font-family: PingFangSC-Regular;
                                font-size: 14px;
                                color: #4A4A4A;
                                letter-spacing: -0.03px;
                                line-height: 18px;
                            }

                            img {
                                width: 9px;
                                margin-left: 2px;
                            }
                        }

                        .redLineBox {
                            width: 20px;
