                            /*margin:0 auto;*/
                            display: flex;
                            align-items: center;
                            position: absolute;
                            bottom: -3px;
                            left: 2px;

                            img {
                                width: 20px;
                            }
                        }
                    }
                }
            }

            .goodsList {
                /*height: 100%;*/
            }

            .goodsBox {
                margin-bottom: 14px;

                .goodsItem {
