                    .goodsImg {
                        width: 85px;
                        height: 85px;
                        overflow: hidden;
                        position: relative;
                        border-radius: 4px;

                        img {
                            width: 100%;
                        }
                        .jinshengNum{
                            width: 100%;
                            height: 25px;
