                            background: rgba(0, 0, 0, 0.50);
                            font-size: 12px;
                            color: #BCBCBC;
                            letter-spacing: -0.04px;
                            line-height: 25px;
                        }
                        .outSellBox{
                            width: 70px;
                            height: 70px;
                            text-align: center;
